.About {
  min-height: 75vh;
  margin-top: 3rem; }
  .About a {
    color: #616161;
    -webkit-transition: color 200ms ease;
    -o-transition: color 200ms ease;
    transition: color 200ms ease; }
    .About a:hover {
      color: #fff !important; }
  .About .about-title {
    margin-bottom: 2rem; }
  .About .row {
    margin-bottom: 0; }
    .About .row.info p:first-child {
      margin-top: 0; }



